function init() {
    const appStateToggle = document.querySelector('.js-appstate-toggle');
    const mobileAppStateToggle = document.querySelector('.js-mobileapp-toggle');
    const appState = document.querySelector("#app");
    const modalWindow = document.querySelectorAll('.js-modal-window');
    const modalWindowLauncher = document.querySelectorAll('.js-modal-toggle');
    const modalWindowClose = document.querySelectorAll('.js-modal-close');
    //Helper Functions
    function appReset(){
        appState.setAttribute('data-app-state', 'sleeping');
        appState.setAttribute('data-modal-state', "inactive");
        for(i=0; i < modalWindow.length; i++){
            modalWindow[i].classList.remove('is-active');
        }
    }
    //App State Toggle Button on Tablet and Deasktop
    appStateToggle.addEventListener('click', function(){
        var currentAppState = appState.getAttribute('data-app-state');
        if(currentAppState == "sleeping") {
            appState.setAttribute('data-app-state', 'active');
        } else {
            appReset();
        }
    });
    //App State Close Button on Mobile Phones
    mobileAppStateToggle.addEventListener('click', function(){
        appReset();
    });
    //Sample Modal Window Launcher
    for (var i = 0; i < modalWindowLauncher.length; i++) {
        modalWindowLauncher[i].addEventListener('click', function(event) {
            var clickedData = this.dataset.sample;
            var modalViewed = document.querySelector(`[data-window="${ clickedData }"]`);
            modalViewed.classList.toggle('is-active');
            appState.setAttribute('data-modal-state', 'active');
        });
    }
    //Sample Modal Window Close Button
    for (var i = 0; i < modalWindowClose.length; i++) {
        modalWindowClose[i].addEventListener('click', function(event){
            const modalCloseButton = event.currentTarget;
            modalCloseButton.parentElement.classList.remove('is-active');
            appState.setAttribute('data-modal-state', 'inactive');
        });
    }
}
init();